import React, { Component } from 'react';
import { AutoSizer, Grid } from 'react-virtualized';
import Navigator from './Navigator';
import ZoomingControls from './ZoomingControls';
import 'url-search-params-polyfill';
import MaterialIcon from 'material-icons-react';
import './Map.css';

const getDistance = (pointA, pointB) => {
    return Math.sqrt(Math.pow(pointA.clientX - pointB.clientX, 2) + Math.pow(pointA.clientY - pointB.clientY, 2));
};

export default class Map extends Component {
    constructor(props) {
        super(props);

        // +2 for paddings
        this._columnCount = this.props.map.columnsCount + 2;
        this._rowCount = this.props.map.rowsCount + 2;

        this._cellWidthMultiplier = 1.5;

        this._scrollTop = 0;
        this._scrollLeft = 0;

        this.state = {
            cellSize: 100,
            scrollTopPercent: 0,
            scrollLeftPercent: 0
        };

        this._map = {};
        this.props.map.data.forEach((row, rowIndex) => {
            row.forEach((x, columnIndex) => {
                this._map[x.id] = { ...x, columnIndex, rowIndex }; 
            });
        });

        const url = new URLSearchParams(window.location.search);
        const hashUrl = new URLSearchParams(window.location.hash.replace("#", ""));

        this._locationId = hashUrl.get("location");
        this._isDebug = url.get("debug") !== null;
    }

    _getColumnSize({index}) {
        if (index === this._columnCount - 1) {
            return this.state.cellSize * 3 * this._cellWidthMultiplier;
        }

        return this.state.cellSize * this._cellWidthMultiplier;
    }

    _getRowSize({index}) {
        if (index === this._rowCount - 1) {
            return this.state.cellSize * 3;
        }

        return this.state.cellSize;
    }

    _cellRenderer({key, columnIndex, rowIndex, style}) {
        if (columnIndex === 0 || columnIndex === this._columnCount - 1) {
            return <div key={key} style={style}></div>;
        }

        if (rowIndex === 0 || rowIndex === this._rowCount - 1) {
            return <div key={key} style={style}></div>;
        }

        const mapObject = this.props.map.data[rowIndex - 1][columnIndex - 1];

        let backgroundColor = "#f6f9f4";
        if (mapObject.withError) {
            backgroundColor = "#ffa3a3";
        }
        else if (mapObject.isLink) {
            backgroundColor = "#bccbe0";
        }
        else if (mapObject.name) {
            backgroundColor = this._locationId == mapObject.id ? "#fff5d1" : "#cadbc5";
        }

        const questLinks = mapObject.links.map(x => {
            const onClickFunc = this._getLinkFunc(x.toId, x.mapName);

            return (
                <span key={x.toId} onClick={onClickFunc} className="quest-link">
                    <MaterialIcon style={{ marginLeft: 3 }} color={"#2b72d8"} size={20 / 100 * this.state.cellSize} icon="error" />
                </span>
            );
        });

        const directions = {};
        mapObject.directions.forEach(x => {
            const color = "#425153";

            switch (x) {
                case "sz": 
                    directions[x] = <div key={x} style={{
                        borderRight: "7px solid transparent",
                        borderTop: "7px solid " + color,
                        position: "absolute", 
                        left: "3px",   // 3px for inside, -1px for outside
                        top: "3px",    // 3px for inside, -1px for outside
                        zIndex: 2
                    }}></div>;
                    break;
                case "s": 
                    directions[x] = <div key={x} style={{
                        border: "5px solid transparent",
                        borderBottomColor: color,
                        position: "absolute", 
                        left: "50%",
                        marginLeft: "-5px",
                        top: "-2px",    // -2px for inside, -7px for outside
                        zIndex: 2
                    }}></div>;
                    break;
                case "sv": 
                    directions[x] = <div key={x} style={{
                        borderLeft: "7px solid transparent",
                        borderTop: "7px solid " + color,
                        position: "absolute", 
                        right: "3px",
                        top: "3px",
                        zIndex: 2
                    }}></div>;
                    break;
                case "v": 
                    directions[x] = <div key={x} style={{
                        border: "5px solid transparent",
                        borderLeftColor: color,
                        position: "absolute", 
                        top: "50%",
                        marginTop: "-5px",
                        right: "-2px",
                        zIndex: 2
                    }}></div>;
                    break;
                case "jv": 
                    directions[x] = <div key={x} style={{
                        borderLeft: "7px solid transparent",
                        borderBottom: "7px solid " + color,
                        position: "absolute", 
                        right: "3px",
                        bottom: "3px",
                        zIndex: 2
                    }}></div>;
                    break;
                case "j": 
                    directions[x] = <div key={x} style={{
                        border: "5px solid transparent",
                        borderTopColor: color,
                        position: "absolute", 
                        left: "50%",
                        marginLeft: "-5px",
                        bottom: "-2px",
                        zIndex: 2
                    }}></div>;
                    break;
                case "jz": 
                    directions[x] = <div key={x} style={{
                        borderRight: "7px solid transparent",
                        borderBottom: "7px solid " + color,
                        position: "absolute", 
                        left: "3px",
                        bottom: "3px",
                        zIndex: 2
                    }}></div>;
                    break;
                case "z": 
                    directions[x] = <div key={x} style={{
                        border: "5px solid transparent",
                        borderRightColor: color,
                        position: "absolute", 
                        top: "50%",
                        marginTop: "-5px",
                        left: "-2px",
                        zIndex: 2
                    }}></div>;
                    break;
                default:
                    break;
            }
        });

        const borderColor = "#95A7AA";

        let cellOnClick = undefined;
        if (mapObject.isLink) {
            cellOnClick = this._getLinkFunc(mapObject.id, mapObject.mapName);
        }
        else if (mapObject.name) {
            cellOnClick = this._selectAndFocusCell.bind(this, mapObject.id, false);
        }

        return (
            <div key={key} style={style} className={mapObject.name ? "cell" : undefined}>
                {Object.values(directions)}
                <div style={{
                    border: "1px dashed " + borderColor,
                    borderLeft: mapObject.name && !mapObject.directions.includes("z") ? "2px solid #425153" : "1px dashed " + borderColor,
                    borderRight: mapObject.name && !mapObject.directions.includes("v") ? "2px solid #425153" : "1px dashed " + borderColor,
                    borderTop: mapObject.name && !mapObject.directions.includes("s") ? "2px solid #425153" : "1px dashed " + borderColor,
                    borderBottom: mapObject.name && !mapObject.directions.includes("j") ? "2px solid #425153" : "1px dashed " + borderColor,
                    position: "absolute", 
                    left: 2, 
                    right: 2, 
                    bottom: 2, 
                    top: 2,
                    fontSize: (this.state.cellSize > 100 ? this.state.cellSize * 0.8 : this.state.cellSize * 1.05) + "%", 
                    textOverflow: "ellipsis",
                    overflow: "hidden",
                    backgroundColor,
                    display: "flex",
                    flexFlow: "row wrap",
                    justifyContent: "center",
                    alignItems: "center"
                }} onClick={cellOnClick}>
                    <span style={{ textAlign: "center" }} onClick={e => e.stopPropagation()}>{ mapObject.name }</span>
                    {questLinks}
                </div>
            </div>
        )
    }

    _getLinkFunc(id, mapName) {
        // default handle for location on another map
        let onClickFunc = (() => { 
            let newLink = "?name=" + mapName;
            if (this._isDebug) {
                newLink += "&debug";
            }
            
            window.location.hash = "#location=" + id;
            window.location.search = newLink;
        }).bind(this);
        
        // if map is the same - it doesn't make sense to reload, just focus new cell
        if (this.props.map.mapName === mapName) {
            onClickFunc = this._selectAndFocusCell.bind(this, id);
        }

        return onClickFunc;
    }

    _zoom(sizeDelta) {
        const cellSize = this._adjustCellSize(this.state.cellSize + sizeDelta);
        if (this.state.cellSize === cellSize) {
            return;
        }
        
        const estimatedWidth = (this._columnCount - 1) * cellSize * this._cellWidthMultiplier + cellSize * 3 * this._cellWidthMultiplier;
        const estimatedHeight = (this._rowCount - 1) * cellSize + cellSize * 3;

        if (this._grid) {
            const newProps = {
                ...this._grid.props, 
                estimatedColumnSize: estimatedWidth / this._columnCount,
                estimatedRowSize: estimatedHeight / this._rowCount,
                scrollTop: this._scrollTop + (sizeDelta * (this._rowCount - 1) + sizeDelta * 3) * this.state.scrollTopPercent,
                scrollLeft: this._scrollLeft + (sizeDelta * this._cellWidthMultiplier * (this._columnCount - 1) + sizeDelta * 3 * this._cellWidthMultiplier) * this.state.scrollLeftPercent
            }

            this._grid.componentWillReceiveProps(newProps);
            this._grid.recomputeGridSize();
        }

        // Percents not changing
        this.setState({
            cellSize,
        });
    }

    _adjustCellSize(size) {
        const minimumWindowSize = window.innerWidth > window.innerHeight ? window.innerHeight : window.innerWidth;

        if (size < minimumWindowSize * 0.05) {
            return minimumWindowSize * 0.05;
        }

        if (size > minimumWindowSize * 0.2) {
            return minimumWindowSize * 0.2;
        }

        return size;
    }

    _selectAndFocusCell(id, needToFocus = true) {
        if (this._locationId == id) {
            return;
        }

        window.location.hash = "#location=" + id;
        this._locationId = id;
        // update grid to display new border
        this._grid.forceUpdate();

        if (needToFocus) {
            // scroll to proper cell
            this._focusCell(id);
        }
    }

    _focusCell(id) {
        if (this._grid) {
            const currentCell = this._map[id];
            if (currentCell) {
                // +1 because we are adding one column and row for padding 
                // and +2 just to make sure Navigator doesn't hide that loc
                this._grid.scrollToCell({columnIndex: currentCell.columnIndex + 3, rowIndex: currentCell.rowIndex + 3});
            }
        }
    }

    _onResize() {
        this._zoom(0);
    }

    _onWheel(event) {
        if (!event.shiftKey) {
            return;
        }

        event.preventDefault();
        const delta = event.deltaY;

        if (delta < 0) {
            this._zoom(10);
        }

        if (delta > 0) {
            this._zoom(-10);
        }
    }

    _onScroll({clientHeight, clientWidth, scrollHeight, scrollWidth, scrollTop, scrollLeft}) {
        if (this._scrollLeft === scrollLeft && this._scrollTop === scrollTop) {
            return;
        }

        this._scrollTop = scrollTop;
        this._scrollLeft = scrollLeft;

        if (this._grid) {
            this.setState({
                scrollLeftPercent: scrollLeft / (scrollWidth - clientWidth + this._grid._verticalScrollBarSize),
                scrollTopPercent: scrollTop / (scrollHeight - clientHeight + this._grid._horizontalScrollBarSize)
            });
        }
    }

    _onNavigatorChange({viewLeft, viewTop}) {
        if (this._grid) {
            this._grid.scrollToPosition({
                scrollTop: viewTop * (this._grid._scrollingContainer.scrollHeight - this._grid._scrollingContainer.clientHeight + this._grid._horizontalScrollBarSize),
                scrollLeft: viewLeft * (this._grid._scrollingContainer.scrollWidth - this._grid._scrollingContainer.clientWidth + this._grid._verticalScrollBarSize)
            });
        }
    }

    _onZoominPlusClick() {
        this._zoom(10);
    }

    _onZoominMinusClick() {
        this._zoom(-10);
    }

    _onTouchStart(event) {
        if (event.touches.length > 1) {
            event.nativeEvent.preventDefault();

            if (event.touches.length === 2) {
                this._initialTouchDistance = getDistance(event.touches[0], event.touches[1]);
            }
        }
    }

    _onTouchMove(event) {
        if (event.touches.length === 2) {
            event.preventDefault();

            this._lastTouchDistance = getDistance(event.touches[0], event.touches[1]);
        }
    }

    _onTouchEnd(event) {
        if (this._initialTouchDistance && this._lastTouchDistance) {
            if (this._lastTouchDistance > this._initialTouchDistance) {
                this._zoom(10);
            }
            else if (this._lastTouchDistance < this._initialTouchDistance) {
                this._zoom(-10);
            }

            this._initialTouchDistance = undefined;
            this._lastTouchDistance = undefined;
        }
    }

    componentDidMount() {
        // 100 ms timeout because it doesn't work other way :D
        setTimeout(() => {
            if (this._locationId !== null) {
                this._focusCell(this._locationId);
            }
        }, 100);

        // window.addEventListener("touchstart", this._onTouchStart.bind(this), false);
        // window.addEventListener("touchmove", this._onTouchMove.bind(this), false);
        // window.addEventListener("touchend", this._onTouchEnd.bind(this), false);
    }

    render() {
        const estimatedWidth = (this._columnCount - 1) * this.state.cellSize * this._cellWidthMultiplier + this.state.cellSize * 3 * this._cellWidthMultiplier;
        const estimatedColumnSize = estimatedWidth / this._columnCount;
        const estimatedHeight = (this._rowCount - 1) * this.state.cellSize + this.state.cellSize * 3;
        const estimatedRowSize = estimatedHeight / this._rowCount;

 
        // onTouchStart={this._onTouchStart.bind(this)} 
        // onTouchMove={this._onTouchMove.bind(this)}
        // onTouchEnd={this._onTouchEnd.bind(this)}

        return (
            <div style={{ position: "absolute", top: 100, bottom: 0, left: 0, right: 0 }} 
                onWheel={this._onWheel.bind(this)}>
                <AutoSizer onResize={this._onResize.bind(this)}>
                    {({width, height}) => (
                        <div>
                            <Grid
                                ref={ ref => { this._grid = ref; }}
                                cellRenderer={this._cellRenderer.bind(this)}
                                columnWidth={this._getColumnSize.bind(this)}
                                rowHeight={this._getRowSize.bind(this)}
                                columnCount={this._columnCount}
                                rowCount={this._rowCount}
                                overscanColumnCount={10}
                                overscanRowCount={10}
                                height={height}
                                width={width}
                                estimatedColumnSize={estimatedColumnSize}
                                estimatedRowSize={estimatedRowSize}
                                onScroll={this._onScroll.bind(this)} />

                            <ZoomingControls onPlusClick={this._onZoominPlusClick.bind(this)} onMinusClick={this._onZoominMinusClick.bind(this)} />
                            <Navigator 
                                width={this._columnCount}
                                height={this._rowCount} 
                                map={this.props.map}
                                viewWidth={width} 
                                viewHeight={height} 
                                viewLeft={this.state.scrollLeftPercent}
                                viewTop={this.state.scrollTopPercent}
                                contentWidth={estimatedWidth} 
                                contentHeight={estimatedHeight}
                                cellWidthMultiplier={1}
                                onChange={this._onNavigatorChange.bind(this)} />
                        </div>
                    )}
                </AutoSizer>
            </div>
        );
    }
}



