import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import Map from './Map';
import registerServiceWorker from './registerServiceWorker';
import 'url-search-params-polyfill';

const getArray = (width, height) => {
    const data = [];

    for (let i = 0; i < height; i++) {
        const row = [];

        for (let j = 0; j < width; j++) {
            if (Math.floor(Math.random() * 10) < 2) {
                row.push({ name: "" });
            }
            else {
                row.push({ name: "" });
            }
        }

        data.push(row);
    }

    return { columnsCount: width, rowsCount: height, data };
}

const getArrayFromLink = async () => {
    let link = "https://swlands.ru/map/generator/data.php?name=";

    let mapName = "academy";
    try {
        const url = new URLSearchParams(window.location.search);
        const urlMapName = url.get("name");
        if (urlMapName !== null) {
            mapName = urlMapName;
        }

        link += mapName;

        if (url.get("debug") !== null) {
            link += "&debug";
        }
    }
    catch (err) {
        link += mapName;
        console.log(err);
    }

    const response = await fetch(link);
    if (response.ok) {
        const data = await response.json();

        return { columnsCount: data.length > 0 ? data[0].length : 0, rowsCount: data.length, data, mapName };
    }

    return { columnsCount: 0, rowsCount: 0, data: [], mapName: "" };
}

getArrayFromLink().then(result => {
    ReactDOM.render(<Map map={result} />, document.getElementById('root'));
});

