import React, { Component } from 'react';
import _ from 'lodash';

class NavigatorMapSvg extends Component {
    shouldComponentUpdate(nextProps) {
        if (nextProps.width === this.props.width && nextProps.height === this.props.height) {
            return false;
        }

        return true;
    }

    render() {
        const cellWidth = this.props.cellWidth * this.props.width / 100;
        const cellHeight = this.props.cellHeight * this.props.height / 100;

        const map = _.flatten(this.props.map.data.map((row, rowNum) => {
            return row.map((x, columnNum) => {
                let dashArray = "0," + cellWidth + "," + (cellHeight + cellWidth) + "," + cellHeight;
                if (columnNum === 0 && rowNum === 0) {
                    dashArray = cellWidth * 2 + cellHeight * 2;
                }
                else if (columnNum === 0) {
                    dashArray = "0," + cellWidth + "," + (cellHeight * cellWidth);
                }
                else if (rowNum === 0) {
                    dashArray = (cellWidth * 2 + cellHeight) + "," + cellHeight;
                }

                return (
                    <rect key={rowNum + ":" + columnNum} 
                        x={cellWidth * (columnNum + 1)}
                        y={cellHeight * (rowNum + 1)}
                        width={cellWidth}
                        height={cellHeight}
                        strokeWidth={0.025 * cellWidth}
                        strokeOpacity={1}
                        stroke="black"
                        strokeDasharray={dashArray}
                        style={{fill: x.name ? "#a9c4a1" : "white" }} />
                );
            });
        }));

        return (
            <svg width={this.props.width} height={this.props.height}>
                {map}
            </svg>
        )
    }
}

class NavigatorMapCanvas extends Component {
    componentDidMount() {
        this._draw();
    }

    componentDidUpdate() {
        this._draw();
    }

    shouldComponentUpdate(nextProps) {
        if (nextProps.width === this.props.width && nextProps.height === this.props.height) {
            return false;
        }

        return true;
    }

    _draw() {
        const cellWidth = this.props.cellWidth * this.props.width / 100;
        const cellHeight = this.props.cellHeight * this.props.height / 100;

        const context = this._canvas.getContext("2d");

        const lineWidth = 0.05 * cellWidth;
        context.lineWidth = lineWidth < 0.5 ? lineWidth : 0.5;
        context.strokeStyle = "black";

        this.props.map.data.forEach((row, rowNum) => {
            row.forEach((data, columnNum) => {
                const x = cellWidth * (columnNum + 1);
                const y = cellHeight * (rowNum + 1);

                context.beginPath();
                context.rect(x, y, cellWidth, cellHeight);
                context.fillStyle = data.name ? "#a9c4a1" : "white";
                context.fill();

                if (columnNum === 0 && rowNum === 0) {
                    context.stroke();
                    return;
                }

                context.beginPath();
                if (columnNum === 0) {
                    // left line
                    context.moveTo(x, y);
                    context.lineTo(x, y + cellHeight);
                }
                if (rowNum === 0) {
                    // top line
                    context.moveTo(x, y);
                    context.lineTo(x + cellWidth, y);
                }

                // right line
                context.moveTo(x + cellWidth, y);
                context.lineTo(x + cellWidth, y + cellHeight);
                // bottom line
                context.moveTo(x, y + cellHeight);
                context.lineTo(x + cellWidth, y + cellHeight);

                context.stroke();
            });
        });
    }

    render() {
        return (
            <canvas ref={ref => this._canvas = ref} width={this.props.width} height={this.props.height} />
        );
    }
}

export default class Navigator extends Component {
    constructor() {
        super();

        this.state = {};
    }

    _onOverlayClick(event) {
        const height = this._holder.clientHeight;
        const width = this._holder.clientWidth;

        let viewWidth = this.props.viewWidth / this.props.contentWidth * width;
        if (viewWidth > width) {
            viewWidth = width;
        }
        let viewHeight = this.props.viewHeight / this.props.contentHeight * height;
        if (viewHeight > height) {
            viewHeight = height;
        }

        let viewLeft = (event.nativeEvent.offsetX - viewWidth / 2) / (event.nativeEvent.target.clientWidth - viewWidth);
        let viewTop = (event.nativeEvent.offsetY - viewHeight / 2) / (event.nativeEvent.target.clientHeight - viewHeight);

        if (viewLeft < 0) {
            viewLeft = 0;
        }

        if (viewTop < 0) {
            viewTop = 0;
        }

        // otherwise parent will update us
        if (this.props.onChange) {
            this.props.onChange({viewLeft, viewTop});
        }
        else {
            this.setState({
                ...this.state, 
                viewLeft,
                viewTop
            });
        }
    }

    update(newState) {
        this.setState({...this.state, ...newState});
    }

    render() {
        let size = 350;
        let percents = false;
        const minimumWindowSize = window.innerWidth > window.innerHeight ? window.innerHeight : window.innerWidth;
        if (minimumWindowSize < 1300) {
            if (minimumWindowSize > 900) {
                size = 270;
            }
            else if (minimumWindowSize > 750) {
                size = 200;
            }
            else if (minimumWindowSize > 500) {
                size = 150;
            }
            else {
                percents = true;
                size = 20;
            }
        }

        let height = this.props.width < this.props.height ? size : this.props.height / this.props.width * size;
        let width = this.props.width > this.props.height ? size : this.props.width / this.props.height * size;

        if (percents) {
            height *= minimumWindowSize / 100;
            width *= minimumWindowSize / 100;
        }

        width *= this.props.cellWidthMultiplier;

        let viewWidth = this.props.viewWidth / this.props.contentWidth * 100;
        if (viewWidth > 100) {
            viewWidth = 100;
        }
        let viewHeight = this.props.viewHeight / this.props.contentHeight * 100;
        if (viewHeight > 100) {
            viewHeight = 100;
        }

        const viewLeft = this.props.viewLeft * (100 - viewWidth);
        const viewTop = this.props.viewTop * (100 - viewHeight);

        // +4 because 1 cell before and triple after as padding
        const cellWidth = 100 / (this.props.map.columnsCount + 4);
        const cellHeight = 100 / (this.props.map.rowsCount + 4);

        return (
            <div ref={ref => this._holder = ref} style={{ 
                position: "absolute", 
                bottom: 20, 
                right: 20, 
                height: height, 
                width: width, 
                backgroundColor: "white",
                border: "1px solid #95A7AA",
                padding: 1,
                zIndex: 10                
            }}>
                <div id={"overlay"} style={{ position: "absolute", left: 0, top: 0, right: 0, bottom: 0, zIndex: 3 }} 
                    onClick={this._onOverlayClick.bind(this)}></div>

                <div id="viewZone" style={{ 
                    position: "absolute", 
                    width: viewWidth + "%", 
                    height: viewHeight + "%",
                    left: viewLeft + "%",
                    top: viewTop + "%",
                    border: "1px solid red",
                    zIndex: 2
                }}></div>

                <NavigatorMapCanvas 
                    cellWidth={cellWidth} 
                    cellHeight={cellHeight} 
                    map={this.props.map} 
                    width={width} 
                    height={height}/>
            </div>
        );
    }
}



