import React, { Component } from 'react';

export default class ZoomingControls extends Component {
    render() {
        return (
            <div style={{
                position: "absolute",
                right: 20,
                top: 5,
                width: 30,
                height: 65
            }}>
                <button style={{ width: 30, height: 30 }} onClick={this.props.onPlusClick}>+</button>
                <button style={{ width: 30, height: 30, marginTop: 5 }} onClick={this.props.onMinusClick}>-</button>
            </div>
        );
    }
}


